
<template>
  <div>
    <!-- 
      :visible="visible" 
      centered 
      :footer="null"  
    -->
    <a-modal
      class="select-assay-modal"
      :visible="visible"
      :closable="false"
      centered
      width="850px"
      cancel-text=""
      @cancel="closeModal"
      ok-text=""
      @ok="confirmImport"
    >
      <this-content ref="selectAssayContent" v-bind="$attrs" />
    </a-modal>
  </div>
</template>
<script>
import thisContent from "./thisContent";

export default {
  components: {
    thisContent,
  },
  props: {
    //, props :title="xxxx"  title="xxxx"
    visible: Boolean, //a-modal 
  },
  data() {
    return {};
  },
  mounted() {},
  methods: {
    /**  */
    closeModal() {
      this.$emit("update:visible", false); //.syncprops :visible.sync
    },
    /**  */
    confirmImport() {
      this.$emit(
        "confirmImport",
        this.$refs.selectAssayContent.selectedItemInfo
      );
      this.closeModal();
    },
  },
};
</script>
