<template>
  <div>
     $attrs:{{$attrs}}
     <br>
     $props:{{$props}}
     <br>
    <a-alert banner message="" description="" type="info" />
    <a-spin tip="Loading..." :spinning="dataSpinning">
      <a-row>
        <a-col :span="4"> </a-col>
        <a-col :span="10">
          <a-input-search
            placeholder="input search text"
            v-model="searchKey"
            @change="handlerSearch"
          />
        </a-col>
        <a-col :span="10"> </a-col>
      </a-row>
      <a-row>
        <a-col :span="4">
          <a-table
            size="small"
            :scroll="{ y: 600 }"
            :pagination="false"
            :columns="table1Columns"
            :data-source="table1Data"
          >
            <template slot="slot1" slot-scope="text, record">
              <div
                v-on:click="handlerItem1Click(record.key)"
                style="cursor: pointer"
              >
                <div
                  v-if="record.key === curSelectItem1"
                  style="
                    background: #08979c;
                    width: 100%;
                    color: #fff;
                    padding: 2px;
                  "
                >
                  {{ record.value }}
                </div>
                <div v-else>{{ record.value }}</div>
              </div>
            </template>
          </a-table>
        </a-col>
        <a-col :span="10">
          <a-table
            size="small"
            :scroll="{ y: 600 }"
            :pagination="pagination"
            :columns="table2Columns"
            :data-source="table2Data"
            :row-selection="{
              selectedRowKeys: selectedRowKeys,
              onChange: handlerSelectChange,
            }"
            @change="handlerTable2Change"
          >
            <template slot="slot2" slot-scope="text, record">
              {{ record.value + "(" + record.searchKey + ")" }}
              <span v-if="'3' === curSelectItem1">
                {{ "(" + record.groupName + ")" }}
              </span>
            </template>
          </a-table>
        </a-col>
        <a-col :span="10">
          <a-table
            size="small"
            :scroll="{ y: 600 }"
            :pagination="false"
            :columns="table3Columns"
            :data-source="table3Data"
          >
            <template slot="slot3" slot-scope="text, record">
              {{ record.typeName + record.value + "  " + record.searchKey + "  " }}
              <span v-if="'' !== record.groupName">
                {{ "(" + record.groupName + ")" }}
              </span>
            </template>
            <template slot="slot3_2" slot-scope="text, record">
              <a-button
                icon="delete"
                size="small"
                type="primary"
                @click="handlerCancelSelect(record.key)"
              ></a-button>
            </template>
          </a-table>
        </a-col>
      </a-row>
    </a-spin>
  </div>
</template>

<script>
export default {
  props: {//, props :title="xxxx"  title="xxxx"
    selectedKeys:[],
    consultationType:String,// 2=- 1=- 3=MDT
  },
  data() {
    return {
      table1Columns: [
        {
          title: "",
          key: "table1_1",
          scopedSlots: { customRender: "slot1" },
        },
      ],
      table2Columns: [
        {
          title: "",
          key: "table2_1",
          scopedSlots: { customRender: "slot2" },
        },
      ],
      table3Columns: [
        {
          title: "",
          key: "table3_1",
          scopedSlots: { customRender: "slot3" },
        },
        {
          title: "",
          width: 60,
          key: "table3_2",
          scopedSlots: { customRender: "slot3_2" },
        },
      ],
      table1Data: [ 
          { key: "2", value: `` },
          { key: "3", value: `` },
          { key: "1", value: `` },
        ],
      table2Data: [],
      table3Data: [], //

      searchKey: "",
      // doctors,
      selectedRowKeys: [], // Check here to configure the default column
      loading: false,
      curSelectItem1: 0,
      dataSpinning: false, //
      pagination: {
        hideOnSinglePage: true,
        pageSize: 15,
        current: 1,
        total: 0,
      }, //hideOnSinglePage 
    };
  },
  //mounted 
  mounted() {
    this.handlerItem1Click("2");//
  },
  computed: {},
  watch:{
    consultationType:{
      handler() {
        if(this.consultationType === '3'){//mdt
          this.table1Data= [
            { key: "2", value: `` },
            { key: "3", value: `` },
          ]
        }else{//
          this.table1Data= [
            { key: "2", value: `` },
            { key: "1", value: `` },
          ]
        }
        this.handlerItem1Click("2");//
      },
      immediate: true, //
    },
  },
  methods: {
    handlerSearch() {
      this.pagination.current = 1;
      if (this.curSelectItem1 === "1") {
        //
        this.getOffice();
      }
      if (this.curSelectItem1 === "3") {
        //
        this.getExperts();
      }
      if (this.curSelectItem1 === "2") {
        //
        this.getDoctors();
      }
    },
    handlerTable2Change(pagination) {
      this.pagination.current = pagination.current;
      if (this.curSelectItem1 === "1") {
        //
        this.getOffice();
      }
      if (this.curSelectItem1 === "3") {
        //
        this.getExperts();
      }
      if (this.curSelectItem1 === "2") {
        //
        this.getDoctors();
      }
    },
    handlerItem1Click(val) {
      this.curSelectItem1 = val;
      this.handlerSearch();
    },
    /**
     * 
     */
    getOffice() {
      this.dataSpinning = true;
      this.table2Data = [];
      let key = this.searchKey.toUpperCase();
      this.$GlobalData.GetOfficesInPage(this.pagination, key).then((res) => {
        res.data.forEach((item) => {
          this.table2Data.push({
            key: item.itemcode,
            id: item.itemcode,
            value: item.itemname,
            searchKey: item.inputcode, //
            groupCode: "",//
            groupName: "",//
            deptCode:item.itemcode,//deptcode null
            pkey: '1',
          });
        });
        this.pagination.total = res.total;
        this.dataSpinning = false;
      });
    },
    /**
     * 
     */
    getDoctors() {
      this.dataSpinning = true;
      this.table2Data = [];
      let key = this.searchKey.toUpperCase();
      this.$GlobalData.GetDoctorsInPage(this.pagination, key).then((res) => {
        console.log("res=", res);
        res.data.forEach((item) => {
          this.table2Data.push({
            key: item.dbuser + item.groupcode,//
            id: item.dbuser,
            value: item.name,
            searchKey: item.inputcode, //
            groupCode: item.groupcode,
            groupName: item.groupname,
            deptCode:item.deptcode,
            pkey: '2',
          });
        });
        this.pagination.total = res.total;
        this.dataSpinning = false;
      });
    },
    /**
     * 
     */
    getExperts() {
      this.dataSpinning = true;
      this.table2Data = [];
      let key = this.searchKey.toUpperCase();
      this.$GlobalData.GetExpertsInPage(this.pagination, key).then((res) => {
        res.data.forEach((item) => {
          this.table2Data.push({
            key: item.ex_code,//
            id: item.ex_code,
            value: item.ex_name,
            searchKey: item.ex_code, //
            groupCode: item.ex_code,
            groupName: item.ex_name,
            deptCode:item.ex_code,
            pkey: '3',
          });
        });
        this.pagination.total = res.total;
        this.dataSpinning = false;
      });
    },    
    handlerSelectChange(selectedRowKeys) {
      this.selectedRowKeys = selectedRowKeys;

      this.table2Data.forEach((item) => {
        if (this.selectedRowKeys.includes(item.key)) {
          let isExit = false;
          this.table3Data.forEach((obj) => {
            if (obj.key == item.key) {
              isExit = true;
            }
          });
          if (!isExit) {
            let _type = "";
            if (this.curSelectItem1 === "1") {
              _type = "";
            }
            if (this.curSelectItem1 === "2") {
              _type = "";
            }
            if (this.curSelectItem1 === "3") {
              _type = "";
            }
            this.table3Data.push({
              id: item.id,
              key: item.key,
              value: item.value,
              groupName: item.groupName,
              groupCode: item.groupCode,
              deptCode: item.deptCode,
              type: this.curSelectItem1,
              typeName: _type,
              searchKey: item.searchKey, //
            });
          }
        }
      });
      //
      this.table3Data.forEach((item) => {
        if (!this.selectedRowKeys.includes(item.key)) {
          this.table3Data.removeValue(item)
        }
      });
    },
    handlerCancelSelect(key) {
      this.table3Data = this.table3Data.filter((item) => {
        return item.key !== key;
      });
      this.selectedRowKeys.forEach((_key) => {
        if (_key === key) {
          this.selectedRowKeys.removeValue(key);
        }
      });
    },
    clearData(){
      // this.table2Data=[]
      this.table3Data=[]
      this.selectedRowKeys=[]
    },
  },
};
</script>