<template>
  <!-- 
      :visible="visible" 
      centered 
      :footer="null"  
    -->
  <div>
    <a-modal
      :visible="visible"
      :title="title+'['+formModel.pat_status+']'"
      centered
      width="1000px"
      @cancel="handlerCancel"
    >
      <!--  -->
      <template slot="footer">
       
        
        <span v-if="formModel.pat_status === 15">
          <a-button
            key="btnDelete"
            type="primary"
            :loading="btnDeleteLoading"
            @click="handlerDelete"
            ></a-button
          >
        </span>
        <span v-if="(formModel.pat_status === 13 && formModel.consultationattr !== '3') || formModel.pat_status === 14">
          <a-button
            key="btnReceive"
            type="primary"
            :loading="btnReceiveLoading"
            @click="handlerReceive"
            ></a-button
          >
        </span>
        <span v-if="formModel.pat_status === 13 && formModel.consultationattr === '3'">
          <a-button
            key="btnCheck"
            type="primary"
            :loading="btnCheckLoading"
            @click="handlerCheck"
            ></a-button
          >
        </span>
        <span v-if="formModel.pat_status === 13 || formModel.pat_status === 2 || formModel.pat_status === 16">
          <a-button key="btnPrint" @click="handlerPrint"></a-button>
        </span>
        <a-button key="btnClose" @click="handlerCancel"></a-button>
      </template>
      <div>
        <a-row type="flex" justify="center" align="top">
          <a-col :span="13">
            <a-spin tip="Loading..." :spinning="formSpinning">
              <a-steps :current="1" size="small">
                <a-step title="" />
                <a-step title="" />
                <a-step title="" />
                <a-step title="" />
                <a-step title="" />
              </a-steps>
              <br />
              <a-descriptions title="" :column="1">
                <a-descriptions-item label="">
                  {{ formModel.name }} | {{ formModel.patientid }} |
                  {{ formModel.visitid }}
                </a-descriptions-item>
                <a-descriptions-item label="">{{
                  formModel.memo
                }}</a-descriptions-item>
                <a-descriptions-item label=""
                  >1111</a-descriptions-item
                >
              </a-descriptions>
              <a-descriptions title="">
                <a-descriptions-item label="">
                  {{ formModel.consultationattr }}
                </a-descriptions-item>
                <a-descriptions-item label="">
                  {{ formModel.applydate }}
                </a-descriptions-item>
                <a-descriptions-item label="">
                  {{
                    $GlobalDict.Consultation.ApprovalStatus.FormatDict(
                      formModel.pat_status
                    )
                  }}
                </a-descriptions-item>
                <a-descriptions-item label="" :span="3">
                  {{ formModel.definitediagnosis }}</a-descriptions-item
                >
                <a-descriptions-item label="" :span="3">
                  {{ formModel.consultationpurpose }}</a-descriptions-item
                >
                <a-descriptions-item label="" :span="3">
                  <span
                    v-for="item in formModel.clsconsultationdetaillist"
                    :key="item.key"
                  >
                    <a-tag :key="item.key" @close="() => handleClose(item.key)">
                      {{ item.groupname + "(" + item.consultationdept + ")" }}
                    </a-tag>
                  </span>
                </a-descriptions-item>
                <a-descriptions-item label="">
                  {{ formModel.applydoctor }}</a-descriptions-item
                >
                <a-descriptions-item label="">{{
                  formModel.phone
                }}</a-descriptions-item>
              </a-descriptions>
              <div v-if="formModel.consultationattr === '3'">
                <a-descriptions title="" :column="3">
                  <a-descriptions-item label="">{{
                    formModel.staging_method
                  }}</a-descriptions-item>
                  <a-descriptions-item label="">
                    {{ formModel.classification_stages }}</a-descriptions-item
                  >
                  <a-descriptions-item label="">{{
                    formModel.primary_tumor
                  }}</a-descriptions-item>
                  <a-descriptions-item label="">{{
                    formModel.lymph_metastasis
                  }}</a-descriptions-item>
                  <a-descriptions-item label="">{{
                    formModel.distant_metastasis
                  }}</a-descriptions-item>
                  <a-descriptions-item label="">{{
                    formModel.by_stages
                  }}</a-descriptions-item>
                </a-descriptions>
              </div>
            </a-spin>
          </a-col>
          <a-col :span="11">
            <view-form-comment
              :folio="initData.primaryKey"
            />
          </a-col>
        </a-row>
      </div>
    </a-modal>
  </div>
</template>
<script>
import viewFormComment from '@/views/Consultation/ViewFormComment'
export default {
  components: {
    viewFormComment,
  },
  props: {
    //, props :title="xxxx"  title="xxxx"
    visible: Boolean, //a-modal 
    initData: {
      //
      type: Object,
      default() {
        return {
          isCreat: false, //
          primaryKey: 0, //
          patientId: "", //
          patientName: "", //
          patientVisitId: "001", //
          patientDiagnosis: "", //
        };
      },
    },
  },
  data() {
    return {
      //
      //Modal
      title: "",
      //FormModel

      btnSaveLoading: false,
      btnSendLoading: false,
      btnDraftApplyLoading: false,
      btnDeleteLoading: false,
      btnReceiveLoading:false,
      btnCheckLoading:false,      
      formSpinning: false, //

      labelCol: { span: 4 },
      labelCol2: { span: 8 },
      wrapperCol: { span: 14 },

      formModel: {},
    };
  },
  mounted() {
    //mounted 
  },
  computed: {
    //  getter
    selectedDoctorsKeys: function () {
      let list = [];
      if (
        typeof this.formModel.clsconsultationdetaillist !== "undefined" &&
        this.formModel.clsconsultationdetaillist != null
      ) {
        this.formModel.clsconsultationdetaillist.forEach((item) => {
          list.push({
            key: item.consultationdept,
          });
        });
      }
      return list;
    },
  },
  watch: {
    initData: {
      handler() {
        this.title = "";
        this.loadData();
      },
      deep: true, //
      // immediate: true, //
    },
  },
  methods: {
    initFormData() {
      this.formModel = {
        consultationpurpose: "",
        applydoctor: "",
        definitediagnosis: "",
        consultationattr: "2", //
        memo: "",
        phone: "",
        staging_method: "",
        clsconsultationdetaillist: [], //
        filelist: [], //
        applydate: null,
        pat_status: -1,
      };
    },
    loadData() {
      if (this.initData.primaryKey !== 0) {
        this.formSpinning = true;
        let url = this.$Api.Consultation.ApplyInfo +"?folio=" +this.initData.primaryKey;
        this.$Http.AsyncPost(url).then((res) => {
          this.formModel = res.data;
          this.formSpinning = false;
        });
      }
    },
    handlerCancel() {
      this.$emit("update:visible", false); //.syncprops :visible.sync
    },
    handlerDelete() {
      this.$refs.editForm.validate((valid) => {
        if (valid) {
          //
          this.btnDeleteLoading = true;
          this.formModel.pat_status = 2;
          this.$Http
            .AsyncPost(this.$Api.Consultation, this.formModel)
            .then((res) => {
              this.btnDeleteLoading = false;
              this.$message.info("");
              this.$emit("update:visible", false); //
              console.log(res);
            });
        } else {
          console.log("error onSave!!");
          return false;
        }
      });
    },
    handlerReceive() {
      this.$refs.editForm.validate((valid) => {
        if (valid) {
          //
          this.btnReceiveLoading = true;
          this.formModel.pat_status = 2;
          this.$Http
            .AsyncPost(this.$Api.Consultation, this.formModel)
            .then((res) => {
              this.btnReceiveLoading = false;
              this.$message.info("");
              this.$emit("update:visible", false); //
              console.log(res);
            });
        } else {
          console.log("error onSave!!");
          return false;
        }
      });
    },
    handlerCheck() {
      alert('')
    },
    
    handlerPrint() {
      console.log("print=");
      console.log("formModel=", this.formModel);
      this.loading = true;
      setTimeout(() => {
        this.loading = false;
        this.$message.info("handlerPrint");
      }, 3000);
    },

    handleClose(removedTag) {
      const tags = this.tags.filter((tag) => tag !== removedTag);
      console.log(tags);
      this.tags = tags;
    },
  },
};
</script>
<style>
.ant-modal-footer button {
  margin-left: 8px;
}
.my_status_flag {
  background: #ccc;
  color: #08979c;
  font-weight: bold;
  float: right;
  border-radius: 30px;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>