<template>
<div>
  <!-- a-table @change  
  :pagination="false" 
  -->
  <a-table 
    :columns="columns"
    :data-source="data"
    :scroll="{ x: 1500}"
    :pagination="pagination"
    :loading="loading"
    size="small"
    @change="handleTableChange"
    rowKey="consulno"
    >
    
    <template slot="receivenumInfo" slot-scope="text, record">
       {{ record.receivenum+'/'+record.receiveall }}
    </template>
    <template slot="patStatus" slot-scope="text, record">
       {{ $GlobalDict.Consultation.ApprovalStatus.FormatDict(record.pat_status) }}
    </template>
    <span slot="action" slot-scope="text, record">
      <template>
        <a @click="openForm({primaryKey:record.consulno,pat_status:record.pat_status})"></a>
      </template>
    </span>
  </a-table>
  <!-- EditFormpropsvisible visible.sync  this.$emit('update:visible',false); props-->
  
  <edit-form
    ref="editForm"
    :visible.sync="editFormVisible"
    :init-data="formInitData"
  />
  <view-form
    ref="viewForm"
    :visible.sync="viewFormVisible"
    :init-data="formInitData"
  />  
</div>
</template>
<script>
import editForm from '@/views/Consultation/ApplyListEditForm'
import viewForm from '@/views/Consultation/ApplyListViewForm'

const columns = [
  { title: '', width: 60, dataIndex: 'bedlabel', key: 'bedlabel', fixed: 'left' },
  { title: '', dataIndex: 'inpno', key: 'age', fixed: 'left' },
  { title: '', dataIndex: 'name', key: 'name',  fixed: 'left'},
  { title: '', dataIndex: 'visitinfowarname', key:'visitinfowarname'},
  { title: '', dataIndex: 'consultationtype', key: 'consultationtype'},
  { title: '', dataIndex: 'consultationdeptlist', key: 'consultationdeptlist', width:'300px', ellipsis: true},
  { title: '', dataIndex: 'consultationdoclist', key: 'consultationdoclist', width:'150px', ellipsis: true},
  { title: '', dataIndex: 'consultationpurpose', key: 'consultationpurpose', ellipsis: true},
  { title: '', dataIndex: 'applydate', key: 'applydate',ellipsis: true, width:'200px'},
  { title: '', key: 'receivenumInfo' ,scopedSlots: { customRender: 'receivenumInfo' },},
  { title: '',  key: 'pat_status',scopedSlots: { customRender: 'patStatus' },},
  {
    title: '',
    key: 'operation',
    fixed: 'right',
    width: 100,
    scopedSlots: { customRender: 'action' },
  },
];


const data = [];

export default {
  components: {
    editForm,
    viewForm,
  },
  props: {//, props :title="xxxx"  title="xxxx"
    queryParam: {},// 
  },
  data() {
    return {//return
      //
      data,
      columns,
      pagination:{hideOnSinglePage:true,pageSize:10000},//hideOnSinglePage 
      loading: true,
      editFormVisible: false,
      viewFormVisible:false,
      formInitData:{},
    };
  },
  mounted() {//mounted 
    this.fetch();
  },
  watch: {
    queryParam: {
      handler() {
        this.fetch();
      },
      deep: true,//
      // immediate: true
    }
  },
  methods: {
    //a-table @change 
    handleTableChange(pagination, filters, sorter) {
      console.log(pagination);
      const pager = { ...this.pagination };
      pager.current = pagination.current;
      this.pagination = pager;
      this.fetch({
        results: pagination.pageSize,
        page: pagination.current,
        sortField: sorter.field,
        sortOrder: sorter.order,
        ...filters,
      });
    },
    //
    fetch() {
      this.loading = true;
      // let getParams = {
      //   results: 10,
      //   ...params,
      // };
      // let postParams = "?applyuser="+this.$GlobalData.LoginUserInfo.userName;//000324
      // if(Object.keys(this.queryParam).length!=0){
      //   getParams += "&patStatus="+ this.queryParam.status; toDate
      // }
      let postParams = {
        "starttime": this.queryParam.applyDateRegion[0].format("YYYY-MM-DD"),
        "endtime": this.queryParam.applyDateRegion[1].format("YYYY-MM-DD"),
        ...this.queryParam,
      };

      this.$Http.AsyncPost(this.$Api.Consultation.ApplyList,postParams).then(res => {
          const pagination = { ...this.pagination };
          // Read total count from server
          // pagination.total = data.totalCount;
          pagination.total = 1;
          this.loading = false;
          this.data = res.data;
          this.pagination = pagination;
      });
    },
    /**  */
    openForm(obj={}) {
      this.formInitData=obj;
      console.log('openForm => this.formInitData =',this.formInitData)
      if(this.formInitData.isCreat || this.formInitData.pat_status===2 || this.formInitData.pat_status===11){
        this.editFormVisible = true;
      }else{
        this.viewFormVisible = true;
      }
    },
  },
};
</script>
