// 
axios.interceptors.response.use(res => {
    // 
    // 1. 
    // 2. 
    if (!res.data.success && res.data.code!=="200") {
        notification.error({
            message: '',
            description: res.data.msg
        })
        return Promise.reject(res.data);
    }else{
        return Promise.resolve(res.data);
    }
},errorHandler)

// jquery ajax 
jquery.ajaxSetup({
    timeout: timeout,
    async:false,
    dataType: 'json',
    contentType: "application/json-patch+json;charset=UTF-8",//data JSON.stringify(params)     
    //
    error: function (xhr,status,error) { errorHandler(error)},
    //successerror
    complete: function (xhr, status) { 
        //XMLHttpRequest
        // var res = JSON.parse(xhr.responseText)
        // try{
        //     if (!res.data.success && res.data.code!=="200") {
        //         notification.error({
        //             message: '',
        //             description: res.data.msg
        //         })
        //         return Promise.reject(res.data);
        //     }
            
        // }catch(e){
            console.log(status)
        // }
    },
    //
    beforeSend: function (xhr) {
        console.log('$.ajaxSetup -> beforeSend1') 
        if(typeof(globalData.LoginUserInfo) !== 'undefined'){
            //
            xhr.setRequestHeader('Access-Token',globalData.AccessToken );
            xhr.setRequestHeader('dbuser', globalData.LoginUserInfo.dbuser);
            xhr.setRequestHeader('username',encodeURIComponent(globalData.LoginUserInfo.username));
            xhr.setRequestHeader('userid', globalData.LoginUserInfo.userid);
            xhr.setRequestHeader('hospitalid',globalData.LoginUserInfo.hospitalid );
        }
    },
})

// 
const errorHandler = (error) => {
        if (error.response) {
        const data = error.response.data
        let tips = error.response.status in httpCode ? httpCode[error.response.status] : error.response.data.message
        //  localstorage  token
        const token = globalData.AccessToken
        if (error.response.status === 403) {
            notification.error({
                message: '',
                description: tips
            })
        }
        if (error.response.status === 401 && !(data.result && data.result.isLogin)) {
            notification.error({
                message: 'Unauthorized',
                description: 'Authorization verification failed'
            })
            if (token) {
                router.push({
                    path: `/login`
                  })
                // store.dispatch('Logout').then(() => {
                //   setTimeout(() => {
                //     window.location.reload()
                //   }, 1500)
                // })
            }
        }
    }
    return Promise.reject(error)
}
let httpCode = {        //http
    400: '',
    401: ', ',
    403: '',
    404: '',
    500: '',
    501: '',
    502: '',
    504: ''
}

export default {
    /**
     * get  jquery 
     * @param {*} url 
     * @param {*} params
     */
    Get: function (url, params) {
        console.log('url=',url)
        let res = null
        jquery.ajax({
            url: baseUrl+url,
            type: "get",
            data: JSON.stringify(params),
            success: function (result) {
                res = result.data
            }
        });
        return res
    },

    /**
     * Posst  jquery 
     * @param {*} url 
     * @param {*} params
     */
    Post: function (url, params) {
        console.log('params=',params)
        let res = null
        jquery.ajax({
            url: baseUrl+url,
            type: "post",
            data: JSON.stringify(params),
            success: function (result) {
                console.log('post->params = ',params)
                res = result.data
            }
        });
        return res
    },

    /**
     * Put  jquery 
     * @param {*} url 
     * @param {*} params
     */
    Put: function (url, params) {
        let res = null
        jquery.ajax({
            url: baseUrl+url,
            type: "put",
            data: JSON.stringify(params),
            success: function (result) {
                res = result.data
            }
        });
        return res
    },
    /**
     * Delete  jquery 
     * @param {*} url 
     * @param {*} params
     */
    Delete: function (url, params) {
        let res = null
        jquery.ajax({
            url: baseUrl+url,            
            type: "delete",
            data: JSON.stringify(params),
            success: function (result) {
                res = result.data
            }
        });
        return res
    },
    /**
     * get  axios
     * @param url 
     * @param auth 
     */
    async AsyncGet(url) {
        return axios.get(url);
    },

    /**
     * post  axios
     *
     * @param url 
     * @param params 
     * @param auth 
     */
    async AsyncPost(url, params) {       
        return axios.post(url, params);
    },

    /**
     * put  axios
     * @param url 
     * @param data 
     * @param auth 
     */
    async AsyncPut(url, data) {
        return axios.put(url, data);
    },

    /**
     *   axios
     * @param url 
     * @param auth 
     */
    async AsyncDel(url) {
        return axios.delete(url);
    },

    /**
     *  axios
     * @param url 
     * @param file 
     */
    Uploader(url, file) {
        let param = new FormData();
        param.append('file', file)
        return axios.post(url, param)
    },
    /**
     * 
     * @param {[Promise,..]} promiseList axios
     */
    async AxiosAll(promiseList){
        //return await this._axiosAll(promiseList)
        return await axios.all(promiseList).then(axios.spread(function(...resList) {
            return resList // posts
        }))
    },
    /**
     * 
     * @param {[Promise,..]} promiseList 
     */
    async _axiosAll(promiseList){
        return axios.all(promiseList).then(axios.spread(function(...resList) {
            return resList // posts
        }))
    },
}