const globalFunc = {
    /**
     * jsurl,url(keyvalue)
     * @param key
     * @returns {string|null}
     */
    getQueryString:(key) => {
        var reg = new RegExp("(^|&)" + key + "=([^&]*)(&|$)", "i");
        var r = window.location.search.substr(1).match(reg);
        if (r !== null) return decodeURI(r[2]); return null;
    },
    /**
     * 
     * @param data ()
     */
    packData:(data) => {
        var vmData={};
        for (var o in data) {
            if (data[o] !== null && data[o] instanceof Array)
                vmData[o]=null;
            else{
                if(typeof data[o] === 'string' && data[o].length > 0)
                    vmData[o]=data[o].trim();
                else
                    vmData[o]=data[o];
            }
        }
        return vmData;
    },
    /**
     * 
     * @param orgObj
     * @param newObj
     * @returns {*}
     * @constructor fg 2019612
     */
    AssignmentObject:(orgObj, newObj) => {
        for (var o in orgObj) {
            if (!(orgObj[o] !== null && orgObj[o] instanceof Array))
            {
                for (var n in newObj){
                    if(o==n){
                        orgObj[o]=newObj[n];
                        break;
                    }
                }
            }
        }
        return orgObj;
    },
    /**
     * 
     * @param data
     */
    clearContent:(data) => {
        var v_data = {};
        for (var o in data) {
            if (data[o] !== null && data[o] instanceof Array)
                v_data[o] = data[o];
            else {
                v_data[o] = null;
            }
        }
        return v_data;
    },
    /**
     *  
     * @param value
     * @returns {boolean}
     */
    isCheck:(value) => {
        value = value.trim();
        if (value === null || value === '' || value === 'undefined')
            return true;
        return false;
    },
}

import moment from 'moment'
globalFunc.moment = moment;//moment 

export default globalFunc
